import { BASE_URL, RESPONSE_OK } from "./constant";
import axios from "axios";
import { setStore, getStore, removeStore } from "./store";
var CryptoJS = require("crypto-js");
const secretKey = "lian";

const Ajax = ({ method, url, data, isFullURL }) =>
  new Promise(async (resolve, reject) => {
    log(1, isFullURL);
    url = isFullURL ? url : BASE_URL + url;

    const token = getStore("token");
    const headers = {
      Authorization: token
    };
    axios({
      method,
      url,
      data,
      timeout: 7000,
      headers
    })
      .then(res => {
        if (
          res.status === 200 &&
          res.data &&
          res.data.returnCode === RESPONSE_OK
        ) {
          if (res.data.encript) {
            let data = res.data.data;
            let bytes = CryptoJS.AES.decrypt(data, secretKey);
            data = JSON.parse(bytes.toString(CryptoJS.enc.Utf8));
            res.data.data = data;
            resolve(res.data);
          } else {
            resolve(res.data);
          }
        } else if (
          res.status === 200 &&
          res.data &&
          res.data.returnCode !== RESPONSE_OK
        ) {
          const err = {
            errCode: res.data.returnCode,
            errMsg: res.data.returnMsg
          };
          reject(err);
        } else {
          const err = {
            errCode: -1,
            errMsg: "请求失败"
          };
          reject(err);
        }
      })
      .catch(error => {
        log(error);
        const err = {
          errCode: -1,
          errMsg: "请求失败"
        };
        reject(err);
      });
  });
const get = (url, data = {}, isFullURL = false) => {
  const array = Object.keys(data);
  url =
    array.length === 0
      ? url
      : url +
        "?" +
        array
          .map(
            item =>
              encodeURIComponent(item) + "=" + encodeURIComponent(data[item])
          )
          .join("&");
  return Ajax({
    url,
    data: {},
    method: "get",
    isFullURL
  });
};

const post = (url, data = {}) =>
  Ajax({
    url,
    data,
    method: "post"
  });

const put = (url, data = {}) =>
  Ajax({
    url,
    data,
    method: "put"
  });

const deletRequest = (url, data = {}) =>
  Ajax({
    url,
    data,
    method: "delete"
  });
export { get, post, put, deletRequest };
